#project
    .project__container
        display: flex
        position: relative
        @media screen and (max-width: 960px)
            flex-direction: column
        .project__content
            width: 50%
            height: 100%
            padding: 50px 80px
            position: sticky
            top: 0
            @media screen and (max-width: 960px)
                width: 100%
                position: static
            @media screen and (max-width: 960px)
                padding: 50px 40px
            h5
                font-family: 'Roboto', sans-serif
                font-size: 16px
                font-weight: 500
                letter-spacing: 1px
                line-height: 25px
            p
                font-size: 15px
                &.project__description
                    margin-bottom: 25px
            .project__details
                margin-bottom: 25px
                a
                    font-family: 'Roboto', sans-serif
                    font-size: 13px
                    text-transform: uppercase
                    line-height: 14px
                    color: #222
                    font-weight: 500
                    text-decoration: none
                    letter-spacing: 1px
                    display: inline-flex
                    align-items: center
                    transition: .3s ease
                    &:before
                        display: flex
                        content: ''
                        width: 20px
                        height: 2px
                        background: #000
                        margin-right: 12px
                        transition: .5s ease
                    &:hover
                        &:before
                            width: 30px
                            background: #41e2ad
        .project__images
            width: 50%
            @media screen and (max-width: 960px)
                width: 100%
            .image
                font-size: 0
                img
                    width: 100%
                    height: 100%
