@use '../abstracts'
@import url('https://fonts.googleapis.com/css2?family=Dosis&family=Playfair+Display:wght@400;700;900&family=Roboto:wght@300;400;500;900&display=swap&family=Hind&display=swap')
//Project Base CSS
*,*:after,*:before
    margin: 0
    padding: 0
    box-sizing: border-box
    scrollbar-color: #000 #fff !important
    scrollbar-width: thin !important
    
body
    height: 100vh
    overflow: auto
::-webkit-scrollbar
    width: 5px
::-webkit-scrollbar-track
    background: #fff
::-webkit-scrollbar-thumb
    background: #000
h1
    @extend %font
    font-size: 60px
    line-height: 82px
h2
    @extend %font
    font-size: 44px
    line-height: 60px

h3
    @extend %font
    font-size: 40px
    line-height: 60px
    @media screen and (max-width: 600px)
        font-size: 28px
        line-height: 52px
h4
    @extend %font
    font-size: 32px
    line-height: 50px

h5
    @extend %font
    font-size: 22px
    line-height: 40px
h6
    @extend %font
    font-size: 13px
    line-height: 28px
p
    font-family: 'Roboto', sans-serif
    font-size: 16px
    font-weight: 300
    color: #606060
    letter-spacing: 0
    line-height: 30px

a.desktop
    font-family: 'Roboto', sans-serif
    font-weight: 400
    font-size: 14px
    color: #232323
    text-transform: uppercase
    text-decoration: none
    line-height: 51px
    letter-spacing: 2px
    transition: all .5s ease
    position: relative
    &:after
        position: absolute
        top: 45px
        left: 0
        height: 2px
        width: 20px
        display: inline-block
        text-align: center
        left: 50%
        transform: translateX(-50%) scale(0,1)
        z-index: 1
        content: ""
        background: #41e2ad
        transition: transform .5s ease
    &.active
        color: #41e2ad
    &:hover
        color: #41e2ad
        &:after
            transform: translateX(-50%) scale(1,1)

a.mobile
    font-family: 'Roboto', sans-serif
    font-weight: 400
    font-size: 14px
    color: #232323
    text-transform: uppercase
    text-decoration: none
    line-height: 51px
    letter-spacing: 2px
    transition: all .5s ease
    &.active
        color: #41e2ad
    &:hover
        color: #41e2ad