.about__container
    display: flex
    // align-items: center
    justify-content: center
    overflow: hidden
    &.contact
        min-height: calc(100vh - 120px)
        @media screen and (max-width: 768px)
            flex-direction: column-reverse
    @media screen and (max-width: 768px)
        flex-direction: column
    .about__image
        flex: 1
        @media screen and (max-width: 768px)
            height: 365px !important
            flex: 0
            overflow: hidden
        img 
            width: 100%
            height: 100%
            object-fit: cover
            // object-position: 5px 10%
            &.noObj
                object-position: 50%
            @media screen and (max-width: 768px)
                height: 365px
    .about__me
        flex: 1
        padding: 150px 
        @media screen and (max-width: 1440px)
            padding: 100px
        @media screen and (max-width: 1024px)
            padding: 60px
        @media screen and (max-width: 425px)
            padding: 30px
        h1
            margin-bottom: 30px
            @media screen and (max-width: 1024px)
                font-size: 44px
            @media screen and (max-width: 768px)
                font-size: 52px
            @media screen and (max-width: 425px)
                font-size: 44px
        h5
            margin-bottom: 30px
            font-family: 'Roboto', sans-serif
            font-family: 'Hind', sans-serif
        h4
            margin-bottom: 15px
            font-size: 23px
            color: #4D4D4D
        .skills
            font-weight: 400
            letter-spacing: 2px
            margin-bottom: 30px
            display: flex
            gap: 10px
            flex-wrap: wrap
            code
                background-color: #999
                color: #fff
                padding: 6px 15px
                border-radius: 2px
                font-family: 'Roboto', sans-serif
                display: inline-flex
                align-items: center
                justify-content: center
                font-size: 14px
                &.html
                    background: #E44D26
                &.css
                    background: #379AD6
                &.js
                    background: #F7E018
                    color: #000
                &.sass
                    background: #CD669A
                &.pug
                    background: #A86454
                &.php
                    background: #858EB8
                &.mysql 
                    background: #4479A1
                &.vs
                    background: #0078D7
                &.boot
                    background: #7709F6
                &.found
                    background: #074E68
                &.bluma 
                    background: #00CAAC
                    color: #000
                &.github
                    background: #161B22
                &.xd
                    background: #440135
                &.tailwind
                    background: #06B0CD
                &.blender
                    background: #E87D0D
        a
            font-family: 'Roboto', sans-serif
            display: inline-flex
            margin-top: 50px
            font-size: 15px
            color: #fff 
            background: #000
            text-decoration: none
            padding: 18px 35px  
            border-radius: 30px
            font-weight: 400
            text-transform: uppercase
            transition: all .5s ease
            &:hover
                background: #41e2ad
    address
        margin-top: 60px
        font-style: regular
        .address,.email
            font-family: 'Dosis', sans-serif
            font-style: normal
            text-transform: uppercase
            letter-spacing: 2px
            
        p
            font-style: normal
            margin-top: 15px
            margin-bottom: 30px
            &.nomar
                margin-bottom: 0
                margin-top: 15px
            &.sm
                margin-top: 5px
                
                