.header__container
    display: flex
    justify-content: space-between
    align-items: center
    padding: 30px 40px
    border-bottom: 1px solid #e9e9e9
    @media screen and (max-width: 425px)
        padding: 20px
    .header__logo
        text-align: center
        a
            text-decoration: none
        .logo__title
            font-family: 'Playfair Display', serif
            font-size: 26px
            font-weight: 900 !important
            letter-spacing: 6px
            color: #000
            text-transform: uppercase
            margin-bottom: 10px
            display: block
        .logo__designation
            font-family: 'Roboto', sans-serif
            text-transform: uppercase
            font-size: 10px
            font-weight: 500
            letter-spacing: 4px
            color: #222
    nav.desktop__nav
        display: flex
        justify-content: center
        align-items: center
        gap: 50px
        .links
            display: flex
            align-items: center
            justify-content: center
            gap: 50px
            @media screen and (max-width: 768px)
                display: none
        .burger
            display: none
            transition: all .5s ease
            align-items: center
            height: 30px
            @media screen and (max-width: 768px)
                display: flex
            .line
                width: 30px
                height: 2px 
                background: #000
                position: relative
                transition: all .5s ease
                &:after,&:before
                    width: 30px
                    height: 2px
                    background: #000
                    content: ''
                    position: absolute
                    transition: all .5s ease
                &:before
                    transform:  translateY(-8px)
                &:after
                    transform:  translateY(8px)
            &.open
                .line
                    transform: translate(-50px)
                    background: transparent
                    &:before
                        transform: rotate(45deg) translate(35px, -35px)
                    &:after
                        transform: rotate(-45deg) translate(35px, 35px)
.header__mobile
    height: 0
    overflow: hidden
    transition: all .5s ease
    display: none
    @media screen and (max-width: 768px)
        display: block
    &.visible
        height: 156px
    nav.mobile__nav
        .links
            display: flex
            flex-direction: column
            a
                border-bottom: 1px solid #e9e9e9
                padding: 0 20px