@use '../../abstracts/_mixins'
            
#newWork
    width: 100% !important
    #projects
        font-size: 0
        .project
            width: 50%
            width: 33.33%
            height: 100%
            display: inline-flex
            position: relative
            // background: #d7d7d7
            margin: 0 
            padding: 0 
            text-decoration: none
            transition: .5s ease
            overflow: hidden !important
            @media screen and (max-width: 1200px)
                width: 50%
            @media screen and (max-width: 768px)
                width: 100%
            @media (hover: none)
                flex-direction: column
                border-bottom: 1px solid #d7d7d7
                margin: 5px 0
            img
                height: 100% !important
                width: 100%
                object-fit: cover
                transition: 2.5s ease
                overflow: hidden
                &:hover
                    // transform: scale(1.1)
            .overlay
                position: absolute
                top: 10px
                right: 10px
                bottom: 10px
                left: 10px
                background: rgba(0,0,0,0.8)
                background: #fff
                display: flex
                flex-direction: column
                align-items: center
                justify-content: center
                opacity: 0
                transition: all .5s ease
                z-index: 10
                pointer-events: none
                @media (hover: none)
                    position: static
                    opacity: 1
                    top: auto
                    bottom: 0
                    right: 0
                    left: 0
                    align-items: flex-start
                    padding: 10px 20px
                    text-decoration: none
                .details
                    text-align: center
                    h5
                        font-family: 'Roboto', sans-serif
                        @extend %hind
                        font-weight: 500
                        font-size: 14px
                        // letter-spacing: 2px
                        line-height: 20px
                        color: #fff
                        color: rgba(0,0,0,0.8)
                        transition: .5s ease
                        text-transform: uppercase
                        position: relative
                        bottom: -15px
                        opacity: 0
                        @media (hover: none)
                            opacity: 1
                            text-align: left
                            bottom: 0
                    p
                        font-family: 'Hind', sans-serif
                        color: #222
                        font-weight: 400
                        transition: .5s ease
                        font-size: 14px
                        position: relative
                        top: -15px
                        opacity: 0
                        font-weight: normal
                        @media (hover: none)
                            opacity: 1
                            top: 0
                            text-align: left

            &:hover
                .overlay
                    opacity: 1
                .details h5
                    bottom: 0
                    opacity: 1
                .details p
                    top: 0
                    opacity: 1

                